import React, { Component } from "react";
import "./App.css";

let Web3 = require("web3");
var web3 = new Web3(new Web3.providers.HttpProvider("https://wallrpc.pirl.io"));

// Nahom - 0xA32dA866506b40235B937A4f94E14708E74F1c83
// James - 0x66502fB281a63015c80F25A0D3821c763F1Df700

//var wallet = "0x66502fB281a63015c80F25A0D3821c763F1Df700";
//var balance = web3.fromWei(web3.eth.getBalance(wallet), "ether") / 1;
//var roundedBalance = Math.round(balance * 100) / 100;

// ==================Hard coded for demonstration =================
var james = "0x66502fB281a63015c80F25A0D3821c763F1Df700";
var nahom = "0xA32dA866506b40235B937A4f94E14708E74F1c83";

var jamesBalance = web3.fromWei(web3.eth.getBalance(james), "ether") / 1;
var jamesRoundedBalance = Math.round(jamesBalance * 100) / 100;

var nahomBalance = web3.fromWei(web3.eth.getBalance(nahom), "ether") / 1;

// ================================================================

console.log("Exact balance " + jamesBalance); //balance without round
console.log("Rounded balance " + jamesRoundedBalance); // balance with PIRL's round

export default class App extends Component {
	constructor() {
		super();
		this.state = {
			// Each input value is initialized to null

			pirlUser: "",
			wallet: "",

			// text input
			walletAddress: "",
			contractNumber: "",
			someNumber: "",
			someText: "",
			// drop down input
			selection1: "",
			selection2: ""
		};
	}

	// The functions below update the input value everytime it
	// is changed and stored in the above variables

	updateContractNumber = event => {
		this.setState({ contractNumber: event.target.value });
		console.log(event.target.value);
	};

	updateSomeNumber = event => {
		this.setState({ someNumber: event.target.value });
		console.log(event.target.value);
	};

	updateSomeText = event => {
		this.setState({ someText: event.target.value });
		console.log(event.target.value);
	};

	// Drop Downs

	updatePirlWallet = event => {
		// function to update Pirl wallet AND USER ***

		this.setState({ walletAddress: event.target.value });
		this.setState({ pirlUser: event.target.value });

		if (this.state.pirlUser === "James") {
			// for some reason this is backwards buts works lol
			this.wallet = nahomBalance;
		} else {
			this.wallet = jamesBalance;
		}
	};

	updateSelection1 = event => {
		this.setState({ selection1: event.target.value });
		console.log(event.target.value);
	};

	updateSelection2 = event => {
		this.setState({ selection2: event.target.value });
		console.log(event.target.value);
	};

	render() {
		return (
			<div className="container">
				<h1>Smart Contract Editor</h1>
				<form id="form_29559" className="appnitro" method="post" action="">
					<div className="form_description">
						<h2>Smart Contract Editor</h2>
						<p>Welcome to the Future</p>
					</div>
					<ul className="list">
						<li id="li_1">
							<label className="description">Select PIRL User</label>
							<div>
								<select
									className="wallet selection"
									id="wallet"
									name="walletOptions"
									value={this.state.walletAddress}
									onChange={this.updatePirlWallet}
								>
									<option value="">Select User</option>
									<option value="James">James</option>
									<option value="Nahom">Nahom</option>
								</select>
								<span id="PIRL"> PIRL: {this.wallet}</span>
							</div>
						</li>
						<li id="li_1">
							<label className="description">Enter the contract number</label>
							<div>
								<input
									placeholder="Contract number"
									value={this.state.contractNumber}
									onChange={this.updateContractNumber}
									className="contract"
									type="text"
									maxLength="255"
								/>
							</div>
						</li>
						<li className="section_break">
							<h3>
								Once the contract is entered, the user would press submit and
								see the content below based on the contract having an array,
								number, enum, and text{" "}
							</h3>
							<p />
						</li>
						<li id="li_5">
							<label className="description">
								Viewable Array Content - If content exists for the array value,
								populate the fields below{" "}
							</label>
							<div>
								<select
									className="element select medium"
									id="element_5"
									name="element_5"
									value={this.state.selection1}
									onChange={this.updateSelection1}
								>
									<option value="1">Choose</option>
									<option value="2">Computer Science</option>
									<option value="3">IT</option>
									<option value="4">IS</option>
								</select>
							</div>
						</li>
						<li id="li_3">
							<label className="description">Some number </label>
							<div>
								<input
									id="element_3"
									name="element_3"
									className="element text medium"
									type="text"
									maxLength="255"
									value={this.state.someNumber}
									onChange={this.updateSomeNumber}
								/>
							</div>
						</li>
						<li id="li_6">
							<label className="description">Some Enum </label>
							<div>
								<select
									className="element select medium"
									id="element_6"
									name="element_6"
									value={this.state.selection2}
									onChange={this.updateSelection2}
								>
									<option value="1">First option</option>
									<option value="2">Second option</option>
									<option value="3">Third option</option>
								</select>
							</div>
						</li>
						<li id="li_4">
							<label className="description">Some Text </label>
							<div>
								<input
									id="element_4"
									name="element_4"
									className="element text medium"
									type="text"
									maxLength="255"
									value={this.state.someText}
									onChange={this.updateSomeText}
								/>
							</div>
						</li>

						<li className="buttons">
							<input type="hidden" name="form_id" value="29559" />

							<input
								id="saveForm"
								className="button_text"
								type="submit"
								name="submit"
								value="Submit"
							/>
						</li>
					</ul>
				</form>
			</div>
		);
	}
}
